<template>
    <div class="haiguan">
        <div class="con">
            <div class="top">
                <div>
                    <el-date-picker v-model="value1" type="date" :placeholder="$t('zhanwei[0]')" value-format="yyyy-MM-dd">
                    </el-date-picker>
                    <span class="fenge"></span>
                </div>
                <div>
                    <el-select v-model="value2" clearable :placeholder="$t('zhanwei[1]')" @change="chaxun()">
                        <el-option v-for="(item,i) in options2" :key="i" :label="item.name" :value="item.id">
                        </el-option>
                    </el-select>
                    <span class="fenge"></span>
                </div>
                <div>
                    <el-input v-model="input" :placeholder="$t('zhanwei[2]')" clearable></el-input>
                </div>
                <div>
                    <el-button type="warning" @click="chaxun()">{{$t('zhanwei[3]')}}</el-button>
                    <!-- <el-button type="success" :disabled='btn' @click="chaxun()">搜索</el-button> -->
                </div>
            </div>
            <div class="table">
                <div class="title">
                    <div>{{$t('listTitle[0]')}}</div>
                    <div>{{$t('listTitle[1]')}}</div>
                    <div>{{$t('listTitle[2]')}}</div>
                    <div>{{$t('listTitle[3]')}}/{{$t('listTitle[4]')}}</div>
                    <div>{{$t('listTitle[5]')}}</div>
                    <div>{{$t('listTitle[6]')}}/{{$t('listTitle[7]')}}</div>
                </div>
            </div>
            <div class="list">
                <div class="item" v-for="(item,i) in list" :key="i">
                    <div>
                        <div class="img">
                            <img :src="imgUrl + item.icon" alt="">
                        </div>
                        <div>
                            <h1>{{item.county}}</h1>
                            <p>{{item.type}}</p>
                        </div>
                    </div>
                    <div>{{item.time}}</div>
                    <div>{{item.name}}</div>
                    <div>{{item.num}}</div>
                    <div>{{item.weight}}</div>
                    <div>{{item.price}}</div>
                </div>
                <div class="no" v-if="show">{{$t('listTitle[8]')}}</div>
            </div>
            <div class="tableFooter">
                <p>{{$t('listTitle[9]')}}{{total_page}}{{$t('listTitle[10]')}}，{{pageTptal}}{{$t('listTitle[11]')}}{{$t('listTitle[12]')}}</p>
                <div>
                    <div>
                        <el-pagination
                            @current-change="handleCurrentChange"
                            :page-size="pageSize"
                            layout="prev, pager, next, jumper"
                            :total="pageTptal">
                        </el-pagination>
                    </div>
                    <div></div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    beforeRouteEnter(to, from, next) {
        return next(vm => {});
    },
    data() {
        return {
            pageSize:15,//每页显示条数
            pageTptal:1,//总条数
            total_page:1,//总页数
            btn:true,//搜索按钮状态
            value1:'',//搜索条件1
            value2:'',//搜索条件2
            input:'',//搜索字段
            options1: [{
                value: 'zh',
                label: '简体中文'
            }, {
                value: 'en',
                label: 'English'
            }],//搜索下拉选1
            options2: [{
                value: '0',
                label: '中国'
            }, {
                value: '1',
                label: '中国'
            }],//搜索下拉选2
            list:'',
            imgUrl:'',
            // shiian:'',//时间
            // county:'',//国家
            // keyword:''//关键词
            show:'',
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        _this.apis.get(_this.apis.countys,{params:{lang:_this.apis.lang}}).then(function(res) {
            if(res.data.code == 0){
                _this.options2 = res.data.data
            }
        })
        _this.getList(1);
    },
    watch:{
        value1(){
            var _this = this;
            if(_this.value1 && _this.value2 && _this.input){
                _this.btn = false;
            }else{
                _this.btn = true;
            }
        },
        value2(){
            var _this = this;
            if(_this.value1 && _this.value2 && _this.input){
                _this.btn = false;
            }else{
                _this.btn = true;
            }
            _this.getList(1,_this.value1,_this.value2,_this.input)
        },
        input(){
            var _this = this;
            if(_this.value1 && _this.value2 && _this.input){
                _this.btn = false;
            }else{
                _this.btn = true;
            }
        },
    },
    methods:{
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        handleCurrentChange(val) {
            var _this = this;
            _this.getList(val);
        },
        getList(p,shiian,county,keyword){
            var _this = this;
            _this.apis.post(_this.apis.customs_data,{page:p,pageNumber:_this.pageSize,date:shiian,c_id:county,keyword:keyword,lang:_this.apis.lang}).then(function(res) {
                if(res.data.code == 0){
                    if(res.data.data.total<1){
                        _this.pageTptal = res.data.data.total
                        _this.total_page = res.data.data.per_page
                        _this.list = res.data.data.data
                        _this.show = true
                        console.log(_this.show)
                    }else{
                        console.log(_this.show)
                        _this.show = false;
                        _this.pageTptal = res.data.data.total
                        _this.total_page = res.data.data.last_page
                        _this.list = res.data.data.data
                    }
                }
            })  
        },
        chaxun(){
            var _this = this;
            _this.getList(1,_this.value1,_this.value2,_this.input)
        },
    },
    components:{
    }
}
</script>

<style lang="less">
.el-select-dropdown{
    margin-top: 12px !important;
}
    .haiguan{
        background: #F4F4F4;
        .con{
            width: 1200px;
            margin: 0 auto;
            padding: 30px;
            .top{
                height: 50px;
                margin-bottom: 30px;
                display: flex;
                justify-content: space-between;
                align-content: center;
                background: #fff;
                box-shadow:  0px 0px 2px #FFAD01;
                border-radius: 7px;
                overflow: hidden;
                &>div{
                    position: relative;
                    .fenge{
                        width: 1px;
                        height: 25px;
                        background: #D2D2D2;
                        position: absolute;
                        right: 0;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }
                .el-select{
                    height: 100%;
                    .el-input{
                        height: 100%;
                    }
                    .el-input__inner{
                        height: 100%;
                    }
                }
                .el-input__inner{
                    border: none;
                    background-color: #fff !important;
                }
                &>div:nth-child(1){
                    width: 180px;
                    padding-right: 15px;
                    .el-input{
                        width: 100%;
                        height: 100%;
                        .el-input__inner{
                            height: 100%;
                        }
                    }
                }
                &>div:nth-child(2){
                    width: 160px;
                    padding-right: 15px;
                }
                &>div:nth-child(3){
                    flex: 1;
                    .el-input{
                        height: 100%;
                    }
                    .el-input__inner{
                        height: 100%;
                    }
                }
                &>div:nth-child(4){
                    width: 130px;
                    .el-button{
                        width: 100%;
                        height: 50px;
                        font-size: 16px;

                    }
                }
            }
            .table{
                padding: 0 50px;
                background: #fff;
                color: #666666;
                font-size: 14px;
                .title{
                    height: 75px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    border-bottom: 1px solid #FFAD01;
                    &>div{
                        border-right: 1px solid #D2D2D2;
                    }
                    &>div:nth-child(1){
                        width: 215px;
                    }
                    &>div:nth-child(2){
                        width: 148px;
                        padding-left: 20px;
                    }
                    &>div:nth-child(3){
                        flex: 1;
                        padding-left: 20px;
                    }
                    &>div:nth-child(4){
                        text-align: right;
                        width: 149px;
                        padding-right: 20px;
                    }
                    &>div:nth-child(5){
                        text-align: right;
                        width: 128px;
                        padding-right: 20px;
                    }
                    &>div:nth-child(6){
                        text-align: right;
                        width: 147px;
                        padding-right: 20px;
                    }
                }
            }
            .list{
                padding: 0 50px;
                background: #fff;
                color: #333333;
                font-size: 14px;
                .no{
                    height: 200px;
                    line-height: 200px;
                    text-align: center;
                    font-size: 16px;
                    color: #c7c7c7;
                }
                &>div:last-child{
                    border-bottom: 1px solid #FFAD01;
                }
                .item{
                    height: 101px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    border-bottom: 1px solid #EEEEEE;
                    &>div:nth-child(1){
                        width: 215px;
                        display: flex;
                        justify-content: left;
                        .img{
                            width: 62px;
                            height:40px;
                            margin-right: 25px;
                            box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.4);
                        }
                        h1{
                            font-size: 16px;
                            color: #FFAD01;
                        }
                        p{
                            font-size: 14px;
                            color: #999999;
                        }
                    }
                    &>div:nth-child(2){
                        width: 148px;
                        padding-left: 20px;
                    }
                    &>div:nth-child(3){
                        flex: 1;
                        padding-left: 20px;
                    }
                    &>div:nth-child(4){
                        text-align: right;
                        width: 149px;
                        padding-right: 20px;
                    }
                    &>div:nth-child(5){
                        text-align: right;
                        width: 128px;
                        padding-right: 20px;
                    }
                    &>div:nth-child(6){
                        text-align: right;
                        width: 147px;
                        padding-right: 20px;
                    }
                }
            }
            .tableFooter{
                background: #fff;
                padding: 0 50px;
                padding-bottom: 20px;
                text-align: center;
                p{
                    font-size: 16px;
                    color: #999;
                    text-align: left;
                }
                &>div{
                    display: inline-block;
                    .el-pagination{
                        margin: 0 auto;
                        .btn-prev{
                            padding: 0;
                        }
                        .btn-prev:hover{
                            .el-icon{
                                color: #FFAD01 !important;
                            }
                        }
                        .btn-next:hover{
                            .el-icon{
                                color: #FFAD01 !important;
                            }
                        }
                        .el-icon:hover{
                            color: #FFAD01 !important;
                        }
                        .active{
                            color: #FFAD01 !important;
                        }
                        .number{
                            color: #666666;
                        }
                        .number:hover{
                            color: #FFAD01;
                        }
                    }
                }
            }
        }
    }
</style>


